import DescriptionsItem from './Item'
import Row from './Row'
export default {
  DescriptionsItem,
  components: {
    Row
  },
  props: {
    title: String,
    bordered: {
      type: Boolean,
      default: false
    },
    column: {
      type: [Number, Object],
      default: 3
    },
    size: {
      type: [Boolean, String],
      default: false,
      enum: ['default', 'middle', 'small']
    },
    layout: {
      type: String,
      default: 'horizontal',
      enum: ['horizontal', 'vertical']
    },
    inline: {
      type: Boolean,
      default: true
    },
    colon: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    rows () {
      const rows = []
      const { column, $slots: { default: nodes } } = this
      let row = []
      let rowSpan = column
      nodes.filter(node => {
        const componentOptions = node.componentOptions || { Ctor: {} }
        const { Ctor: { options } } = componentOptions
        return options ? options.DESCRIPTIONS_ITEM : false
      })
        .forEach((node, index) => {
          const span = node.componentOptions.propsData.span || 1
          if (index === nodes.length - 1) {
            row.push(node)
            rows.push(row)
            return
          }

          if (span < rowSpan) {
            rowSpan -= span
            row.push(node)
          } else {
            row.push(node)
            rows.push(row)
            rowSpan = column
            row = []
          }
        })
      return rows
    }
  },
  render () {
    const { title, rows, bordered, size, colon, $slots, layout, inline } = this
    return (
      <div class={{
        'ant-descriptions': true,
        [`ant-descriptions-${size}`]: true,
        'ant-descriptions-bordered': !!bordered
      }}>
        {title && <div class='ant-descriptions-title'>{title}</div>}
        {!title && $slots.title && <div class='ant-descriptions-title'>{$slots.title}</div>}
        <div class="ant-descriptions-view">
          <table>
            <tbody>
              {rows.map((nodes) => {
                if (layout === 'vertical') {
                  return ([
                    <Row> {nodes.map(node => {
                      const { span, label, align } = node.componentOptions.propsData
                      return (<th class="ant-descriptions-item-label" style={{ 'text-align': align }} colSpan={span}>{label}</th>)
                    })} </Row>,
                    <Row> {
                      nodes.map(node => {
                        const { span, align } = node.componentOptions.propsData
                        return (<td class="ant-descriptions-item-content" style={{ 'text-align': align }} colSpan={span}>{node}</td>)
                      })
                    } </Row>
                  ])
                }
                return (<Row> {
                  nodes.map(node => {
                    const { label, span, align } = node.componentOptions.propsData
                    return bordered || !inline ? [
                      <th class="ant-descriptions-item-label">{label}</th>,
                      <td class="ant-descriptions-item-content" style={{ 'text-align': align }} colSpan={span ? span * 2 - 1 : ''}>{node}</td>
                    ] : <td class="ant-descriptions-item" colSpan={span} style={{ 'text-align': align }}>
                      <span class={{
                        'ant-descriptions-item-label': true,
                        'ant-descriptions-item-colon': colon
                      }}>{label}</span>
                      <span class="ant-descriptions-item-content">{node}</span>
                    </td>
                  })
                } </Row>)
              })}
            </tbody>
          </table>
        </div>
      </div>
    )
  },
  renderError () {
  }
}
